import React, { Component } from 'react'
import { Accordion, Card } from 'react-bootstrap'

import EnergiGas from '../images/energigas.png'
import FlytandeBransle from '../images/flytande.png'
import Cement from '../images/cement.png'
import GasFackling from '../images/gasfackling.png'
import FastBransle from '../images/fast.png'
import ArrowDown from '../images/arrow_down.png'

export default class Rullgardin extends Component {
    render() {
        return (
            <div>
                <h3>Gr ett val i menyn nedan fr att lra dig mer</h3>
                <Accordion>
                    <Card>
                        <Accordion.Toggle as={Card.Header} eventKey="0">
                            <img className="co2-ikoner" src={EnergiGas} alt="Ikon fr energigas" />
                            Energigaser <img className="arrowdown" src={ArrowDown} alt="Pil som pekar nedt" />
                        </Accordion.Toggle>
                        <Accordion.Collapse eventKey="0">
                            <Card.Body>
                                Energigaser r brnslen som anvnds i gasform. Alla dessa r fossila brnslen och kallas ven 
                                vxthusgaser eftersom de bidrar till uppvrmningen av jorden.
                            </Card.Body>
                        </Accordion.Collapse>
                    </Card>
                    <Card>
                        <Accordion.Toggle as={Card.Header} eventKey="1">
                            <img className="co2-ikoner" src={FlytandeBransle} alt="Ikon fr flytande brnsle" />
                            Flytande brnslen <img className="arrowdown" src={ArrowDown} alt="Pil som pekar nedt" />
                        </Accordion.Toggle>
                        <Accordion.Collapse eventKey="1">
                            <Card.Body>
                                Petroleum, som ven kallas fr rolja, mineralolja eller bergolja r en vtska 
                                med hga halter av grundmnet kol. Produkter som produceras av petroleum r bensin, 
                                diesel och gasol. De flesta av vra bilar, bussar, mopeder och flygplan drivs idag
                                med flytande brnslen.
                            </Card.Body>
                        </Accordion.Collapse>
                    </Card>
                    <Card>
                        <Accordion.Toggle as={Card.Header} eventKey="2">
                            <img className="co2-ikoner" src={Cement} alt="Ikon fr cement" />
                            Cement <img className="arrowdown" src={ArrowDown} alt="Pil som pekar nedt" />
                        </Accordion.Toggle>
                        <Accordion.Collapse eventKey="2">
                            <Card.Body>
                            Cement r ett viktigt rmaterial i byggnadsindustrin och anvnds i stor utstrckning vid 
                            framstllning av betong och murbruk. Cement framstlls genom uppvrmning av kalksten och lera. D 
                            uppvrmningen krver vrme upp till 1500 grader celsius r framstllningen mycket energikrvande 
                            och frbrnningen leder till utslpp av koldioxid.
                            </Card.Body>
                        </Accordion.Collapse>
                    </Card>
                    <Card>
                        <Accordion.Toggle as={Card.Header} eventKey="3">
                            <img className="co2-ikoner" src={GasFackling} alt="Ikon fr gasfackling" />
                            Gasfackling <img className="arrowdown" src={ArrowDown} alt="Pil som pekar nedt" />
                        </Accordion.Toggle>
                        <Accordion.Collapse eventKey="3">
                            <Card.Body>
                            Gasfackling innebr att man brnner upp verskottsgas p oljeflt och i oljeindustrin utan 
                            att ta tillvara vrmeenergin. Gasfackling sker i stor skala och kunde utnyttjas som fjrrvrme 
                            eller omvandlas till andra drivmedel istllet fr att lta den outnyttjad brinna upp.
                            </Card.Body>
                        </Accordion.Collapse>
                    </Card>
                    <Card>
                        <Accordion.Toggle as={Card.Header} eventKey="4">
                            <img className="co2-ikoner" src={FastBransle} alt="Ikon fr fast brnsle" />
                            Fasta brnslen <img className="arrowdown" src={ArrowDown} alt="Pil som pekar nedt" />
                        </Accordion.Toggle>
                        <Accordion.Collapse eventKey="4">
                            <Card.Body>
                            Av fossila brnslen i fast form r stenkol och brunkol de allra mest knda och har anvnts 
                            i rtusenden. Kolkraftverk producerar elektricitet och spillenergin anvnds i form av fjrrvrme.
                            </Card.Body>
                        </Accordion.Collapse>
                    </Card>
                </Accordion>
            </div>
        )
    }
}
