import React, { Component } from 'react'
import { Jumbotron, Row, Col } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import './Stil.css'
import ButtonClose from '../images/closebutton.png'
import CO2 from '../images/koldioxid2.jpeg'
import KoldioxidData from './KoldioxidData'
import RullGardin from './Rullgardin'
import AudioPlayer from './AudioPlayer'
import Ljudfil from '../audio/koldioxid.mp3'

export default class Koldioxid extends Component {
    render() {
        return (
            <Jumbotron>
                <Row>
                    <Col>
                        <Link to="/Hem">
                        <img className="btn-close" src={ButtonClose} alt="Knapp fr att terg till startsidan" />
                        </Link>
                    </Col>
                </Row>
                <Row>
                    <Col className="coltext">
                <h1>Koldioxidutslpp och hur det pverkar klimatet</h1>
                <p>
                Koldioxid r en viktig gas fr vxter. Du har skert hrt om fotosyntesen, dr vxter med hjlp av 
                solenergi och vatten omvandlar koldioxid till nring fr vxterna. Restprodukten r syre som anvnds 
                av djur och mnniskor. 
                </p>
                <p>
                Nr vxter och djur dr frmultnar de. Efter miljontals r omvandlas de frmultnade resterna som nu finns djupt in 
                i marken till fossiler. Riktigt gamla fossiler blir till petroleum och naturgas. Av dessa kan mnniskan
                idag utvinna olika slag av brnslen, som alla kallas fr fossila brnslen.
                </p>
                <p>
                Nr mnniskan anvnder fossila brnslen slpps ocks koldioxid ut i luften.
                Pltsligt finns det mycket mer koldioxid i luften, mer n vad vxtriket efterfrgar. Det extra koldioxidet 
                bildar ett gaslager hgt upp i atmosfren. Lite som ett vxthus, som hindrar vrme att lmna atmosfren.
                </p>
                <p>
                Det r tack vare fossila brnslen som vi har kunnat utveckla vrlden s snabbt sedan 1800-talet. 
                Fossila brnslen har gett mnniskan en enorm kraft att ka snabbare, producera mer saker, justera 
                vrme, kyla och mycket mer. Hela den moderna vrlden r uppbyggd genom fossila brnslen. r 2019 r fossila
                brnslen fortfarande en mycket stor klla till vrldens energifrbrukning d de str fr 80% av frbrukningen.
                </p>
                <p>
                Fr att kunna minska de fossila utslppen krvs alternativa energiformer, det som kallas fr frnybar energi. 
                Exempel p frnybar energi r sol, vind-och vattenkraft. Det tar lng tid att vxla energikllor i hela vrlden. 
                Drfr kommer det behvas ny teknik som under tiden tar bort koldioxid ur atmosfren. Det heter negativa utslpp 
                och r mycket positivt, trots namnet.
                </p>
                <AudioPlayer ljud={{fil: Ljudfil}} />
                    </Col>
                    <Col>
                        <img className="jumbotronpic2" src={CO2} alt="Koldioxidets kretslopp" />
                    </Col>
                </Row>
                <Row className="rowbottom">
                <Col>
                    <KoldioxidData />
                </Col>
                <Col className="col_rull">
                    <RullGardin />
                </Col>
                </Row>
            </Jumbotron>
        )
    }
}
