import React, { Component } from 'react'
import { Jumbotron, Row, Col } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import './Stil.css'
import ButtonClose from '../images/closebutton.png'
import GlaciarSeaData from './GlaciarSeaData'
import GlaciarPic from '../images/isberg.jpeg'
import AudioPlayer from './AudioPlayer'
import Ljudfil from '../audio/glaciar.mp3'

export default class Glaciar extends Component {
    render() {
        return (
            <Jumbotron>
                <Row>
                    <Col>
                        <Link to="/Hem">
                        <img className="btn-close" src={ButtonClose} alt="Knapp fr att terg till startsidan" />
                        </Link>
                    </Col>
                </Row>
                <Row>
                <Col className="coltext">
                <h1>Smltande glacirer och stigande havsniv</h1>
                <p>
                P jorden finns det drygt 21 miljoner kubikkilometer (km3) is. Det r vrldens strsta stvattenreserv. 
                Nr is smlter blandas stvatten med havsvatten och stvattenreserven minskar. Forskarna sger att om 
                all vrldens is smlter och rinner ner i havet s skulle havsnivn stiga med 110 meter.
                </p>
                <p>
                Fre r 2100 berknar forskarna att havsytan kommer att stiga ver en meter. Det pverkar hundratals miljoner 
                mnniskor som bor vid havet runt om i vrlden. Speciellt drabbade blir folk som bor p ar, srskilt i Stillahavet, 
                dr arnas hgsta punkt ibland ligger bara ngra meter ver havet. Deras hem riskerar att frsvinna fr alltid!
                </p>
                <p>
                Mnga stora stder ligger ocks precis vid havet och dr skulle det drfr ocks bli stora problem om havsnivn stiger. 
                Inte bara stder utan ven vgar och jrnvgar skulle hamna under vatten. Stora delar av 
                mnga kustnra svenska stder skulle frsvinna under ytan. ven stora stder som Stockholm, Gteborg och Malm 
                skulle drabbas hrt.
                </p>
                <p>
                Nr glacirer smlter kommer den mrkare marken fram. En mrk yta vrms upp snabbare n en vit yta. Det r ett 
                exempel p hur klimatfrndringar vid en viss situation skyndas p av frndringen i sig. Frutom mnniskan, drabbas 
                mnga djurarter av frndringarna, d deras naturliga livsmiljer frndras eller frsvinner.
                </p>
                <AudioPlayer ljud={{fil: Ljudfil}} />
                </Col>
                <Col>
                    <img className="jumbotronpic" src={GlaciarPic} alt="Smltande glacir och isberg" />
                </Col>
                </Row>
                <Row className="rowbottom">
                <Col>
                    <GlaciarSeaData />
                    <p className="glac-text">
                    Diagrammet ovan berttar att havsnivn har hjts med nstan 15 centimeter mellan<br/> ren 1945 och 2010.
                    Under samma tid har 25 000 kubikkilometer (km3) is smlt.
                    </p>
                </Col>
                </Row>
            </Jumbotron>
        )
    }
}
