import React, { Component } from 'react'
import { Jumbotron, Row, Col } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import './Stil.css'
import ButtonClose from '../images/closebutton.png'
import TempPic from '../images/temp.jpeg'
import TemperaturData from "./TemperaturData"
import AudioPlayer from './AudioPlayer'
import Ljudfil from '../audio/temperatur.mp3'

export default class Temperatur extends Component {
    render() {
        return (
            <Jumbotron>
                <Row>
                    <Col>
                        <Link to="/Hem">
                        <img className="btn-close" src={ButtonClose} alt="Knapp fr att terg till startsidan" />
                        </Link>
                    </Col>
                </Row>
                <Row>
                <Col className="coltext">
                <h1>Jordens medeltemperatur kar</h1>
                <p>
                Jordens temperaturkning r skapad av oss mnniskor och kallas fr vxthuseffekt. 
                Genom vxthuseffekten stannar solens vrme kvar p jorden - likt vrmen i ett vxthus.
                </p>
                <p>
                Nr vi blickar upp emot himlen knns atmosfren ondlig.  Men i sjlva verket r den bara en liten tunn hinna 
                p 100 kilometer som skiljer jorden frn rymden. Men den delen som pverkar vdret och innehller syre r endast 
                717 kilometer hg. Nr vi frstr att vi bara har en dryg mil syre runt hela planeten frstr vi varfr 
                atmosfren inte tl s mycket utslpp och varfr temperaturen kan hjas i atmosfren. 
                </p>
                <p>
                Under de senaste 100 ren har jordens medeltemperatur kat med nstan 1 grad. Nu r mlet att den inte skall bli 
                mer n 2 grader. Ibland kan det kanske knnas som att en grad inte r s mycket. Men en grads medeltemperatur betyder i 
                sjlva verket flera graders variation runt om i vrlden. Vi hr redan idag om kade stormar, mer torka p vissa 
                stllen och versvmningar p andra stllen. Dessa r ibland en direkt konsekvens av klimatfrndringarna.
                </p>
                <p>
                Mnga ekosystem r oerhrt knsliga fr temperaturfrndringar och redan nu har ver hlften av vrldens 
                alla ekosystem pverkats nr jordens medeltemperatur kat med 1 grad. Forskning visar att om temperaturen 
                kar mer n 3 grader, s kar den drefter av egen kraft och mnniskan kan inte stoppa det.
                </p>
                <AudioPlayer ljud={{fil: Ljudfil}} />
                </Col>
                <Col>
                    <img className="jumbotronpic" src={TempPic} alt="Bild som visar vxthuseffekten" />
                </Col>
                </Row>
                <Row className="rowbottom">
                <Col>
                    <TemperaturData />
                </Col>
                </Row>
            </Jumbotron>
        )
    }
}
